.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 1);
  padding: 20px 80px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
  z-index: 10;
  position: relative;
}

.navbar-logo-img {
  height: 62px;
}

.nav-link {
  color: rgb(0, 0, 0);
}

.login-link {
  color: rgb(0, 0, 0);
}

.navbar-right {
  display: flex;
  align-items: center;
  font-size: 1.3rem;
  font-weight: 600;
  transition: 0.3s ease-in-out;
}

.cart-icon {
  color: rgb(0, 0, 0);
  padding-left: 1.5rem;
  margin-top: 0.5em;
}

.navbar-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  font-weight: 600;
}

.navbar-nav li {
  list-style: none;
  padding: 0 20px;
  position: relative;
  transition: 0.3s ease-in-out;
}

.cart-link {
  text-decoration: none;
  display: flex;
  align-items: center;
}

.navbar-nav :hover {
  color: lightslategray;
  text-decoration: underline;
}

.navbar-right :hover {
  color: lightslategray;
  text-decoration: underline;
}

.login-link:hover {
  text-decoration: underline;
}

.cart-icon:hover {
  color: lightslategray;
  text-decoration: underline;
}

.cart-count {
  position: absolute;
  top: -2px;
  right: -7px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  background-color: #ff0000;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  border-radius: 50%;
}

.logout-button {
  color: rgb(0, 0, 0);
  border: 0;
  font-size: 1em;
  font-weight: 520;
  background-color: #ffffff;
  font: bold;
}

.dropdown-toggle {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0;
  color: rgb(0, 0, 0);
}

.dropdown-menu {
  display: none;
  position: absolute;
  background-color: rgba(255, 255, 255, 1);
  padding: 10px;
  z-index: 1;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.6);
}

.dropdown-item {
  display: block;
  padding: 5px;
  color: rgb(0, 0, 0);
  text-decoration: none;
}

.dropdown-item:hover {
  background-color: rgba(0, 0, 0, 0.1);
  color: lightslategray;
}

.dropdown:hover .dropdown-menu {
  display: block;
}
